<template>
    <div class="card">
      <h2>{{ title }}</h2>
      <p>{{ description }}</p>
      <pre><code ref="codeBlock" class="language-html" v-html="code"></code></pre>
    </div>
  </template>
  
  <script>
  import hljs from 'highlight.js'
  import 'highlight.js/styles/github.css' 
  
  export default {
    name: 'ComparisonCard',
    props: {
      title: String,
      description: String,
      code: String
    },
    mounted() {
      this.highlight()
    },
    updated() {
      this.highlight()
    },
    methods: {
      highlight() {
        const block = this.$refs.codeBlock
        if (block) hljs.highlightElement(block)
      }
    }
  }
  </script>
  
  <style scoped>
  .card {
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 1rem;
    margin: 1rem 0;
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,0.05);
  }
  pre {
    background: #f6f8fa;
    padding: 1rem;
    overflow-x: auto;
    border-radius: 5px;
  }
  </style>
  